<template>
  <div align='center'>
    <v-container>
        <v-card>
          <v-layout row wrap class="justify-center">
            <v-flex xs10>
              <v-card-title class="justify-center">SSO Testing Hub</v-card-title>
              <v-text>
                This is a simple tool to help test, debug, and demonstrate
                the single sign on feature of our senior project. This tool
                was designed for GetUsGrub, Extracurricular Schooling, CareAway,
                and WhatFits.
              </v-text>
            </v-flex>
          </v-layout>
          <v-layout row wrap class="justify-center">
            <v-flex xs8>
              <v-text-field
                label="Username"
                v-model="username"
                required
              />
              <v-text-field
                label="Password"
                v-model="password"
                :min="8"
                :counter="64"
                :append-icon="pwVisible ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (pwVisible = !pwVisible)"
                :type=" pwVisible ? 'text' : 'password'"
                :error-messages="errorMessages"
                required
                @input="validatePassword()"
              />
              <v-text-field
                label="Front-end Url"
                v-model="feUrl"
                required
              />
              <v-text-field
                label="Back-end Url"
                v-model="beUrl"
                required
              />
              <v-text-field
                label="Application"
                v-model="application"
                required
              />
              <v-text-field
                label="Role"
                v-model="roleType"
                required
              />
              <v-text-field
                label="Secret Key"
                v-model="secretKey"
                :append-icon="secretVisible ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (secretVisible = !secretVisible)"
                :type=" secretVisible ? 'text' : 'password'"
                required
              />
            </v-flex>
          </v-layout>
          <v-layout row justify-center>
          <v-btn
          id="endpoint-btn"
          color="primary"
          @click="send('Registration')">
            Register
          </v-btn>

          <v-btn
          id="endpoint-btn"
          color="primary"
          @click="login()">
            Login
          </v-btn>

          <v-btn
          id="endpoint-btn"
          color="primary"
          @click="send('ResetPassword')">
            Reset Password
          </v-btn>
          </v-layout>
          <v-layout column justify-center id="response-field">
            <v-text-field
              label="Endpoint"
              :value="fullUrl"
              multi-line
              disabled
            />
            <v-text-field
              label="Token"
              :value="token"
              multi-line
              disabled
            />
            <v-text-field
              label="Response"
              :value="response"
              multi-line
              disabled
            />
        </v-layout>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import PasswordValidation from '@/components/PasswordValidation';
import jwt from 'jsonwebtoken';
import axios from 'axios';
import swal from 'sweetalert2';

export default {
  name: 'TestingHub',
  components: { },
  data: () => ({
    username: 'TestUser123',
    password: 'thisisaverysecurepassword',
    application: 'getusgrub',
    roleType: 'public',
    pwVisible: false,
    secretVisible: false,
    errorMessages: [],
    secretKey: '',
    feUrl: 'http://localhost:8080',
    beUrl: 'http://localhost:8081',
    fullUrl: '',
    token: '',
    response: '',
  }),
  methods: {
    validatePassword() {
      if (this.password.length < 8) {
        this.errorMessages = [];
        return;
      }
      PasswordValidation.methods.validate(this.password)
        .then((response) => {
          this.errorMessages = response.message;
        });
    },
    generateToken() {
      const time = Math.floor(Date.now() / 1000);
      return jwt.sign({
        username: this.username,
        password: this.password,
        roleType: this.roleType,
        application: this.application,
        iat: time,
      },
      this.secretKey);
    },
    login() {
      this.token = this.generateToken();
      const url = `${this.feUrl}/SingleSignOn?jwt=${this.token}`;
      window.location.href = url;
    },
    send(endpoint) {
      this.token = this.generateToken();
      this.fullUrl = `${this.beUrl}/Sso/${endpoint}`;
      const authHeader = {
        'Access-Control-Allow-Origin': 'https://fannbrian.github.io',
        'Access-Control-Allow-Credentials': true,
        Accept: 'application/json',
        'Content-Type': 'application/json',
        Authorization: `Bearer ${this.token}`,
        'X-Requested-With': 'XMLHttpRequest',
      };
      console.log(authHeader);
      axios({
        method: 'POST',
        url: this.fullUrl,
        headers: authHeader,
      }).then((response) => {
        this.response = `${response.status}: ${response.data}`;
        swal(endpoint, `${response.status} : ${response.data}`, 'success');
      }).catch((ex) => {
        this.response = ex;
        swal(endpoint, `${ex}: ${ex.message}`, 'error');
      });
    },
  },
  created() {
    const num = Math.floor(Math.random() * 9999999);
    this.username = `TestUser${num}`;
  },
};
</script>

<style>
#endpoint-btn {
  max-width: 250px;
}
#response-field {
  align-content: center;
  justify-content: center;
  max-width: 750px;
}
.swal2-popup {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
</style>



