<script>
import axios from 'axios';
import sha1 from 'sha1';

export default {
  name: 'PasswordValidation',
  components: {},
  methods: {
    validate(password) {
      const hash = sha1(password);
      const anonHash = hash.substring(0, 5);
      const promise = axios.get(`https://api.pwnedpasswords.com/range/${anonHash}`,
      ).then((response) => {
        const data = response.data;
        const lines = data.split('\n');

        for (let i = 0; i < lines.length; i += 1) {
          const line = lines[i].split(':');

          if ((anonHash + line[0]).toLowerCase() === hash) {
            const count = line[1];

            if (count >= 100) {
              return ({
                isValid: false,
                message: `Highly breached password: (${count} counts)`,
              });
            } else if (count !== 0) {
              return ({
                isValid: true,
                message: `Breached password (${count} counts)`,
              });
            }
          }
        }

        return ({
          isValid: true,
          message: [],
        });
      });
      return promise;
    },
  },
};
</script>



